<template>
    <div class="c-tab" id="c-tab">
        <div class="tab-nav">
            <div v-for="(item,i) in this.tabs.data"
                v-on:click="tab_click(item.index)"
                :class="['tab-nav-item',tab.index == item.index ? 'active':'']" 
            ><span>{{item.text}}</span></div>
        </div>
        <div class="tab-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(item,i) in this.tabs.data">
                    <component :is="apiTabs[item.val].component | humpStrToLinkStr()"></component>
                </div>
            </div>
            
            <!-- 宝箱 Start -->
            <box-component @lotteryShowJson="lotteryShowJson"></box-component>
            <!-- 宝箱 End -->
            <confirm-component :data="confirmObj" v-if="confirmObj.type" @close="confirmObj = {}" @confirm="confirm"></confirm-component>
        </div>
    </div>
</template>
<script>
import Swiper from 'swiper';

import ChatComponent from '@/components/ChatComponent.vue'
import RankComponent from '@/components/RankComponent.vue'
import RichTextComponent from '@/components/RichTextComponent.vue'
import BoxComponent from '@/components/BoxComponent.vue'
import ConfirmComponent from '@/components/ConfirmComponent'

import {mapMutations, mapState, mapGetters} from 'vuex'

export default {
    name: "TabComponent",
    components:{
        ChatComponent,
        RankComponent,
        RichTextComponent,
        BoxComponent,
        ConfirmComponent,
    },
    data(){
        return {
            tab:{
                index: 0,
                tab_container: null,
            },
            apiTabs: this.ApiConfig.tabs,
            confirmObj: {}
        }
    },
    computed:{
        ...mapState({
            chnid: state => state.BaseInfoService.chnid,
            baseInfo: state => state.BaseInfoService.baseInfo,
        }),
        ...mapGetters(['tabs']),
    },
    methods:{
        tab_change(index){
            this.tab.index = index;
        },
        tab_click(index){
            this.tab_change(index);
            this.tab.tab_container.slideTo(index, 500, false);//切换到第一个slide，速度为1秒
        },
        lotteryShowJson(data){
            if(data.success == 'true'){
                this.confirmObj = data;
            }else{
                this.$store.commit('alert',{
                    text: data.msg
                })
            }
        },
        confirm(params){
            this.$store.dispatch('enrollJson',params).then(({data}) => {
                this.confirmObj = {}
                this.$store.commit('alert',{
                    text: data.msg
                })
            })
        },
    },
    filters:{
        //  驼峰转连字符
        humpStrToLinkStr(str){
            return str.replace(/([A-Z])/g,"-$1").toLowerCase().replace(/^\-/,"");
        }
    },
    mounted: function(){
        this.$nextTick(() => {
            var _this = this;
            
            this.tab.tab_container = new Swiper('.tab-container', {
                on: {
                    slideChangeTransitionStart: function(){
                        if (_this.isAndroid){
                            $('#chat-input').blur();
                        }
                        _this.tab_change(this.activeIndex)
                    },
                },
                observer:true,      //修改swiper自己或子元素时，自动初始化swiper
                observeParents:true,//修改swiper的父元素时，自动初始化swiper
            });
            //  调整到激活tab
            this.tab_click(this.tabs.activeIndex)
        });
    }
}
</script>



